ul.search-results img {
  margin: 0 auto;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 350px;
  /* margin: 1rem; */
  position: relative;
  width: 270px;
  box-shadow: 0 5px 8px #6f6666, 0 5px 8px #6f6666;
  text-align: left;
  border-radius: .95rem;
  margin-bottom: 15px;
}

.card > .img-container {
  height: 70%;
  overflow: hidden;
  text-align: center;
  background: white;
  border-radius: .95rem;
}
.card > .img-container > img {
  width: 70%;
  padding-top: 1rem;
}

.card > .content {
  padding-top: 0px;
  padding-left: -2rem;
  padding-right: -2rem;
  font-size: 15px;
  
}

.card > .content > ul {
  list-style-type: none;
  margin-left: 0px;
}

.card > .content > ul > li {
  margin-top: 8px;
  margin-bottom: 0;
  margin-top: 0px;
  padding-top: 2px;
  padding-bottom: 0px;
}

.h3, h3 {
  font-size: 20px;
  font-weight: bold;
}

.list-group-item {
  position: relative;  
  background-color: transparent;
  width: 950px;
  border-radius: .95rem;
  font-size: small;  
}


.hero {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

form.search {
  overflow: auto;
  margin-bottom: 10px;
  /* width: 800px;  */
  flex-direction: row;
}

form.search button[type="submit"] {
  float: right;
  margin-top: 1px;
  border-radius: .95rem;
  font-size: small;
  box-shadow: 0 2px 2px #999, 0 2px 2px #999;
}

.form-control {
  display: block;
  width: 100%;
  /* height: calc(1.5em + .75rem + 2px); */
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .85rem;
  box-shadow: 0 2px 2px #999, 0 2px 2px #999;
  font-size: 12px;
  -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.image {
  padding-top: 0px;
  padding-bottom: 0px;
  margin-top: -10px;
  border-radius: .95rem;
  box-shadow: 0 8px 12px #999, 0 8px 12px #999;
}

.form-group {
  display: flex;
  flex-direction: row;
}


ul.search-results img {
  margin: 0 auto;
}

.list-group-item {
  position: relative;  
  background-color: transparent;
  width: 950px;
  border-radius: .95rem;
  font-size: small;
}

.result-box {
  padding-top: 23px;
}

.btn-success {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
  border-radius: .95rem;
  box-shadow: 0 2px 2px #999, 0 2px 2px #999;
  margin-left: 5px;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}
ul.search-results img {
  margin: 0 auto;
}
nav.navbar.navbar-default {
  margin-bottom: 0;
}
.footer {
  /* height: 10px; */
  background: rgb(95, 95, 95);
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: rgb(56, 55, 55);
  color: whitesmoke;
  height: 50px;
  font-weight: bold;
  text-align: center;
}

.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
  font-size: 18px;
  
}

